import React from "react"

// reactstrap components
import { Container } from "reactstrap"
import BackgroundSlider from 'react-background-slider'

// import img1 from "../../images/Construction/Construction1.jpg"
import img2 from "../../images/Construction/Construction2.jpg"
import img3 from "../../images/Construction/Construction3.jpg"
import img4 from "../../images/Construction/Construction4.jpg"
import img5 from "../../images/Construction/Construction5.jpg"
import img6 from "../../images/Construction/Construction6.jpg"
import img7 from "../../images/Construction/Construction7.jpg"
import img8 from "../../images/Construction/Construction8.jpg"
import img9 from "../../images/Construction/Construction9.jpg"


class PageHeader extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      clip_path: "clip-top",
    }
  }
  componentDidMount() {
    window.addEventListener("scroll", this.changeColor)
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.changeColor)
  }
  changeColor = () => {
    if (
      document.documentElement.scrollTop > 99 ||
      document.body.scrollTop > 99
    ) {
      this.setState({
        clip_path: "clip-scrolled",
      })
    } else if (
      document.documentElement.scrollTop < 100 ||
      document.body.scrollTop < 100
    ) {
      this.setState({
        clip_path: "clip-top",
      })
    }
  }

  render() {
    return (
        
      <div id="pageHeader" className={"page-header header-filter " + this.state.clip_path}>
          <BackgroundSlider
        images={[img2,img3,img4,img5,img6,img7,img8,img9]}
        duration={5} transition={2} />
        <div className="squares square1" />
        <div className="squares square2" />
        <div className="squares square3" />
        <div className="squares square4" />
        <div className="squares square5" />
        <div className="squares square6" />
        <div className="squares square7" />

        <Container style={{height:"100vh"}}>

        {this.props.children}</Container>
      </div>

    )
  }
}

export default PageHeader
